dor-logo\">\n                        <img\n                            src=\"/assets/vendors/dashboard/ven-logo-upload.png\"\n                            alt=\"\"\n                        />\n                        <span>Add</span>\n                        <input\n                            type=\"file\"\n                            name=\"vendor-logo\"\n                            id=\"vendor-logo\"\n                            hidden\n                        />\n                    </label>\n                </div> -->\n            </div>\n            <div :class=\"[loader ? 'blur row about' : 'row about']\">\n                <div class=\"btn-container\">\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn active\"\n                        data-id=\"vendor-details\"\n                        title=\"Accomodation Details\"\n                        @click=\"toggleForm($event, true)\"\n                    ></button>\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn\"\n                        data-id=\"vendor-add-room\"\n                        title=\"Room Details\"\n                        @click=\"\n                            toggleForm($event, true);\n                            showRoomPics();\n                        \"\n                    ></button>\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn\"\n                        data-id=\"vendor-about\"\n                        title=\"Gallery\"\n                        @click=\"toggleForm($event, true)\"\n                    ></button>\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn\"\n                        data-id=\"vendor-add-space\"\n                        title=\"Amenities\"\n                        @click=\"toggleForm($event, true)\"\n                    ></button>\n                </div>\n                <div class=\"about-content forms\">\n                    <form\n                        @submit.prevent=\"submitForm1($event)\"\n                        data-id=\"vendor-add-room\"\n                        class=\"content active\"\n                        id=\"vendor-details\"\n                    >\n                        <h1>Accomodation details</h1>\n                        <div\n                            v-if=\"formEdit.form1\"\n                            style=\"display: flex; justify-content: flex-end\"\n                            class=\"editbtn\"\n                        >\n                            <button\n                                class=\"shake\"\n                                @click=\"enableFeilds($event)\"\n                                style=\"\n                                    outline: none;\n                                    border: none;\n                                    background-color: #461952;\n                                    color: white;\n                                    padding: 4px 20px;\n                                    border-radius: 10px;\n                                    margin-bottom: 10px;\n                                    margin-right: 20px;\n                                \"\n                                type=\"button\"\n                            >\n                                Edit\n                            </button>\n                        </div>\n                        <div class=\"vendor-details-form-fields\">\n                            <label for=\"accName\">\n                                Name\n                                <input\n                                    type=\"text\"\n                                    name=\"vendorFirstName\"\n                                    id=\"vendorFirstName\"\n                                    placeholder=\"Enter your first name\"\n                                    v-model=\"addAccomodation.name\"\n                                />\n                                <span\n                                    v-if=\"errors && errors.name\"\n                                    class=\"errMsg\"\n                                    >{{ errors.name[0] }}</span\n                                >\n                            </label>\n                            <label for=\"accPhone1\">\n                                Phone Number - 1\n                                <input\n                                    type=\"text\"\n                                    name=\"accPhone1\"\n                                    id=\"accPhone1\"\n                                    placeholder=\"Enter accomodation contact number\"\n                                    v-model=\"addAccomodation.number_1\"\n                                />\n                                <span\n                                    v-if=\"errors && errors.number_1\"\n                                    class=\"errMsg\"\n                                    >{{ errors.number_1[0] }}</span\n                                >\n                            </label>\n                            <label for=\"accPhone2\">\n                                Phone Number - 2\n                                <input\n                                    type=\"text\"\n                                    name=\"accPhone2\"\n                                    id=\"accPhone2\"\n                                    placeholder=\"Enter accomodation contact number\"\n                                    v-model=\"addAccomodation.number_2\"\n                                />\n                                <span\n                                    v-if=\"errors && errors.number_2\"\n                                    class=\"errMsg\"\n                                    >{{ errors.number_2[0] }}</span\n                                >\n                            </label>\n                        </div>\n                        <div class=\"vendor-address-form-fields\">\n                            <!-- <label for=\"accType\">\n                                Accomodation type\n                                <select\n                                    v-model=\"addAccomodation.type\"\n                                    name=\"accType\"\n                                    id=\"accType\"\n                                >\n                                    <option hidden disabled selected>\n                                        Select vendor’s category\n                                    </option>\n                                    <option\n                                        v-for=\"item in subCategories\"\n                                        :key=\"item.id\"\n                                        :value=\"item.id\"\n                                    >\n                                        {{ item.name }}\n                                    </option>\n                                </select>\n                                <span\n                                    v-if=\"errors && errors.type\"\n                                    class=\"errMsg\"\n                                    >{{ errors.type[0] }}</span\n                                >\n                            </label> -->\n                            <label for=\"accType\">\n                                Services\n                                <select\n                                    name=\"vendorCategories\"\n                                    id=\"vendorCategories\"\n                                    v-model=\"serviceId\"\n                                    multiple\n                                    style=\"margin-top: 10px; padding: 10px\"\n                                >\n                                    <option hidden disabled selected>\n                                        Select vendor’s category\n                                    </option>\n                                    <option\n                                        v-for=\"item in services\"\n                                        :key=\"item.id\"\n                                        :value=\"item.id\"\n                                    >\n                                        {{ item.name }}\n                                    </option>\n                                </select>\n                                <span\n                                    v-if=\"errors && errors.services\"\n                                    class=\"errMsg\"\n                                    >{{ errors.services[0] }}</span\n                                >\n                            </label>\n                            <label for=\"accTypeStar\">\n                                Accomodation Stars\n                                <select\n                                    v-model=\"addAccomodation.stars\"\n                                    name=\"accType\"\n                                    id=\"accType\"\n                                >\n                                    <option hidden disabled selected>\n                                        Select vendor’s category\n                                    </option>\n                                    <option value=\"1\">1</option>\n                                    <option value=\"2\">2</option>\n                                    <option value=\"3\">3</option>\n                                    <option value=\"4\">4</option>\n                                    <option value=\"5\">5</option>\n                                </select>\n                                <span\n                                    v-if=\"errors && errors.stars\"\n                                    class=\"errMsg\"\n                                    >{{ errors.stars[0] }}</span\n                                >\n                            </label>\n                            <label for=\"accAddress\">\n                                Address Line 1\n                                <input\n                                    type=\"text\"\n                                    name=\"accAddress\"\n                                    id=\"accAddress\"\n                                    placeholder=\"Enter address\"\n                                    v-model=\"addAccomodation.address1\"\n                                />\n                                <span\n                                    v-if=\"errors && errors.address1\"\n                                    class=\"errMsg\"\n                                    >{{ errors.address1[0] }}</span\n                                >\n                            </label>\n                            <label for=\"accAddress1\">\n                                Address Line 2\n                                <input\n                                    type=\"text\"\n                                    name=\"accAddress1\"\n                                    id=\"accAddress1\"\n                                    placeholder=\"Enter address\"\n                                    v-model=\"addAccomodation.address2\"\n                                />\n                                <span\n                                    v-if=\"errors && errors.address2\"\n                                    class=\"errMsg\"\n                                    >{{ errors.address2[0] }}</span\n                                >\n                            </label>\n                            <label for=\"accstate\">\n                                State\n                                <select\n                                    v-model=\"addAccomodation.state\"\n                                    name=\"accstate\"\n                                    id=\"accstate\"\n                                    @change=\"getDistricts($event)\"\n                                >\n                                    <option hidden disabled selected>\n                                        Select vendor’s category\n                                    </option>\n                                    <option\n                                        v-for=\"(item, index) in states\"\n                                        :key=\"item.name\"\n                                        :value=\"item.name\"\n                                    >\n                                        {{ item?.name }}\n                                    </option>\n                                </select>\n                                <span\n                                    v-if=\"errors && errors.state\"\n                                    class=\"errMsg\"\n                                    >{{ errors.state[0] }}</span\n                                >\n                            </label>\n                            <label for=\"accdistrict\">\n                                District\n                                <select\n                                    v-model=\"addAccomodation.location_id\"\n                                    name=\"accdistrict\"\n                                    id=\"accdistrict\"\n                                >\n                                    <option hidden disabled selected>\n                                        Select vendor’s category\n                                    </option>\n                                    <option\n                                        v-for=\"(item, index) in district\"\n                                        :key=\"index\"\n                                        :value=\"item.id\"\n                                    >\n                                        {{ item.name }}\n                                    </option>\n                                </select>\n                                <span\n                                    v-if=\"errors && errors.location_id\"\n                                   