ner form-upload-vue\">\n                <svg\n                    width=\"30\"\n                    height=\"20\"\n                    viewBox=\"0 0 20 15\"\n                    fill=\"none\"\n                    xmlns=\"http://www.w3.org/2000/svg\"\n                >\n                    <path\n                        d=\"M3.54979 1.3875C-2.63254 5.39784 5.09979 11.4625 9.74979 13.7875C14.3998 13.0125 22.1498 4.48748 16.7248 1.3875C14.546 0.142315 11.2998 2.1625 9.74979 3.7125C8.19979 2.42084 5.11741 0.370628 3.54979 1.3875Z\"\n                        fill=\"#FF5BFF\"\n                        stroke=\"#FF5BFF\"\n                        stroke-width=\"1.55\"\n                    />\n                </svg>\n                <div>\n                    <svg\n                        width=\"70\"\n                        height=\"68\"\n                        viewBox=\"0 0 49 47\"\n                        fill=\"none\"\n                        xmlns=\"http://www.w3.org/2000/svg\"\n                    >\n                        <path\n                            d=\"M46.83 23.75C46.83 35.3256 37.1128 44.83 24.975 44.83C12.8371 44.83 3.11995 35.3256 3.11995 23.75C3.11995 12.1744 12.8371 2.67 24.975 2.67C37.1128 2.67 46.83 12.1744 46.83 23.75Z\"\n                            stroke=\"white\"\n                            stroke-width=\"4.34\"\n                        />\n                    </svg>\n                    <svg\n                        width=\"70\"\n                        height=\"68\"\n                        viewBox=\"0 0 49 47\"\n                        fill=\"none\"\n                        xmlns=\"http://www.w3.org/2000/svg\"\n                    >\n                        <path\n                            d=\"M46.83 23.75C46.83 35.3256 37.1128 44.83 24.975 44.83C12.8371 44.83 3.11995 35.3256 3.11995 23.75C3.11995 12.1744 12.8371 2.67 24.975 2.67C37.1128 2.67 46.83 12.1744 46.83 23.75Z\"\n                            stroke=\"white\"\n                            stroke-width=\"4.34\"\n                        />\n                    </svg>\n                </div>\n            </div>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n\n<script>\nimport Services from \"../Services.vue\";\nimport flashMessage from \"../../../FlashMessage.vue\";\n\nexport default {\n    components: {\n        Services,\n        flashMessage,\n    },\n    props: [\n        \"vendor\",\n        \"vendorCat\",\n        \"subcat\",\n        \"selectedSubCat\",\n        \"services\",\n        \"servicesSelected\",\n    ],\n\n    data() {\n        return {\n            showImg: false,\n            errors: null,\n            vendorDetails: JSON.parse(this.vendor),\n            vendorCategories: JSON.parse(this.vendorCat),\n            subCategories: JSON.parse(this.subcat),\n            accomodation_id: null,\n            form: false,\n            added: false,\n            hidden: false,\n            message: null,\n            loader: false,\n            serviceId: this.servicesSelected ?? [],\n            sendService: [],\n            cont: [],\n            locationMaster: [],\n            states: [],\n            district: [],\n            image: null,\n            imagePreview: [],\n            videoPreview: [],\n            newimagePreview: [],\n            newvideoPreview: [],\n            roomimagePreview: [],\n            roomvideoPreview: [],\n            roomnewimagePreview: [],\n            roomnewvideoPreview: [],\n            addSpaceSubmit: {},\n            formSubmitted: {\n                form1: false,\n                form2: false,\n                form3: false,\n                form4: false,\n            },\n            formEdit: {\n                form1: false,\n                form2: false,\n                form3: false,\n                form4: false,\n            },\n            formUpdate: {\n                form1: false,\n                form2: false,\n                form3: false,\n                form4: false,\n            },\n            addAccomodation: {\n                name: null,\n                number_1: null,\n                number_2: null,\n                stars: null,\n                address1: null,\n                address2: null,\n                state: null,\n                location_id: null,\n                zipcode: null,\n                country: null,\n                latitude: null,\n                longitude: null,\n                map: null,\n            },\n            addAccomodation2: {\n                veg_plate: null,\n                non_veg_plate: null,\n                facebook_link: null,\n                instagram_link: null,\n                twitter_link: null,\n                snapchat_link: null,\n                about: null,\n                features: null,\n            },\n            addRoomSubmit: {\n                room_type: null,\n                sleeps: null,\n                beds_available: null,\n                total_rooms: null,\n                price_per_room: null,\n                tax_per_room: null,\n                room_specifications: [],\n                room_facilities: [],\n            },\n            roomSpecificationTags: [\n                { id: 1, name: \"City view\", img: \"la_city.svg\" },\n                { id: 2, name: \"Flat-screen TV\", img: \"cil_tv.svg\" },\n                { id: 3, name: \"Ensuite bathroom\", img: \"cil_tv.svg\" },\n                { id: 4, name: \"Air conditioning\", img: \"cil_tv.svg\" },\n                { id: 5, name: \"Free WiFi\", img: \"cil_tv.svg\" },\n            ],\n            accomoFacilitiesTags: [\n                { id: 1, name: \"Free toiletries\" },\n                { id: 2, name: \"Shower safety\" },\n                { id: 3, name: \"Linen sockets\" },\n                { id: 4, name: \"Alaram clock\" },\n                { id: 5, name: \"Hair dryer\" },\n                { id: 6, name: \"Wakeup service\" },\n            ],\n        